function monitor() {
	if (window.addEventListener) {
		window.addEventListener("deviceorientation", function(e) {
			//$(".chaptertitle>div").html(parseInt(e.beta))
			
			var beta = parseInt(e.beta)
			var v = scene.getCameraView();

			CameraView = (beta * 0.02)  //
			CubeAngle = beta*-1
			
			if(details[chapter-1].list.length==3){
				CameraView=CameraView+0.65
				CubeAngle=(CubeAngle*9)+90
				
				if (CameraView >= 0.82) {
					CameraView = 0.82
				}
				if (CameraView <= 0.49) {
					CameraView = 0.49
				}
				
				if(CubeAngle<=0){
					CubeAngle=0
				}
				if(CubeAngle>=180){
					CubeAngle=180
				}
				
			}
			if(details[chapter-1].list.length==4){
				CameraView=CameraView+0.6
				CubeAngle=(CubeAngle*10)+125
				
				
				
				if (CameraView >= 0.82) {
					CameraView = 0.82
				}
				if (CameraView <= 0.32) {
					CameraView = 0.32
				}
				
				if(CubeAngle<=0){
					CubeAngle=0
				}
				if(CubeAngle>=270){
					CubeAngle=270
				}
			}
			if(details[chapter-1].list.length==5){
				CameraView=CameraView+0.5
				CubeAngle=(CubeAngle*9.3)+180
				
				
				//console.log(CameraView)
				//console.log(CameraView)
				if (CameraView >= 0.82) {
					CameraView = 0.82
				}
				if (CameraView <= 0.12) {
					CameraView = 0.12
				}
				if(CubeAngle<=0){
					CubeAngle=0
				}
				if(CubeAngle>=360){
					CubeAngle=360
				}
				
				if(CubeAngle>180){
					scene.switchCubeImage(0,kvUrl+'6_1_5.jpg?v2','')
				}else{
					scene.switchCubeImage(0,kvUrl+'6_1_1.jpg?v2','')
				}
				
			}
			upPanelNDC()
			scene.setCameraView(CameraView);
			scene.setCubeAngle(CubeAngle);
		})
	}
}

