function playVideo() {

	$(".DeviceBox").hide()
	$(".video").show()

	$(".Android").remove()

	audio.play();
	// document.addEventListener("WeixinJSBridgeReady", function() {
	// 	audio.play();
	// }, false);
	audio.addEventListener('play', () => {
		audio.currentTime = 1.5
	});
	audio.addEventListener('playing', () => {
		setInterval(() => {
			if (audio.currentTime >= 58) {
				audio.currentTime = 44.03
			}
		}, 100)
		setInterval(() => {
			var currentTime = Math.floor(audio.currentTime)

			if (currentTime >= 2 && currentTime < 6) {
				if ($(".videotext1").css("display") == "block") {
					return
				}
				$(".videotext1").fadeIn()
			}

			if (currentTime >= 6) {
				$(".videotext1").fadeOut()
			}

			if (currentTime >= 8 && currentTime < 12) {
				if ($(".videotext2").css("display") == "block") {
					return
				}
				$(".videotext2").fadeIn()
			}

			if (currentTime >= 12) {
				$(".videotext2").fadeOut()
			}

			if (currentTime >= 13 && currentTime < 17) {
				if ($(".videotext3").css("display") == "block") {
					return
				}
				$(".videotext3").fadeIn()
			}

			if (currentTime >= 17) {
				$(".videotext3").fadeOut()
			}

			if (currentTime >= 18 && currentTime < 24) {
				if ($(".videotext4").css("display") == "block") {
					return
				}
				$(".videotext4").fadeIn()
			}
			if (currentTime >= 24) {
				$(".videotext4").fadeOut()
			}
			if (currentTime >= 26 && currentTime < 32) {
				if ($(".videotext5").css("display") == "block") {
					return
				}
				$(".videotext5").fadeIn()
			}
			if (currentTime >= 32) {
				$(".videotext5").fadeOut()
			}
			if (currentTime >= 33 && currentTime < 39) {
				if ($(".videotext6").css("display") == "block") {
					return
				}
				$(".videotext6").fadeIn()
			}
			if (currentTime >= 39) {
				$(".videotext6").fadeOut()
			}
			if (currentTime >= 41 && currentTime < 46) {
				if ($(".videotext7").css("display") == "block") {
					return
				}
				$(".videotext7").fadeIn()
			}
			if (currentTime >= 46) {
				$(".videotext7").fadeOut()
			}
			if (currentTime >= 47) {
				$(".videotextBox").remove()
				$(".tiaoguo").fadeOut()
			}

			if (currentTime > 48) {
				videocurrentTime()
			}

		}, 500);
	});
	return
	if (isiOS) {

	} else {
		$(".ios").remove()

		var Decode = []

		var canvas = document.getElementById('video-canvas');
		var url =
			'https://video.play.atline.cn/a5b00908vodsh1252054921/2b7db4d9243791579132483817/RZYojMPnaeQA.ts';
		var player = new JSMpeg.Player(url, {
			canvas: canvas,
			loop: false,
			autoplay: true,
			onSourceCompleted: function(e) {

			},
			onVideoDecode(decoder, time) {
				Decode.push(decoder)
				if (Decode.length > 700) {
					videocurrentTime()
				}
			},
			onPlay: function(e) {
				setTimeout(() => {
					$(".Android").fadeIn()
				}, 500)
			},
			onEnded: function() {},
			onStalled(player) {

			}
		});
	}
}

function videocurrentTime() {
	if (isPornum == 0) {
		$(".tshp").fadeIn().removeClass("b000")
	}
}

window.addEventListener("onorientationchange" in window ? "orientationchange" : "resize", isPortrait, false);

function isPortrait() {
	if (isiOS) {
		if (!window.DeviceOrientationEvent.requestPermission) {
			return
		}
	}

	if (window.orientation === 180 || window.orientation === 0) {
		if (isPornum > 0) {
			$(".primaryBox").hide()
			$(".tshp").show().addClass("b000")
		}
		setTimeout(() => {
			if (isPornum > 0) {
				$(".primaryBox").hide()
				$(".tshp").show().addClass("b000")
			}
		}, 200)
		return true;
	}
	if (window.orientation === 90 || window.orientation === -90) {
		setTimeout(() => {
			$(".primaryBox").show()
			$(".tshp").hide()
			$(".video").remove()
			$(".DeviceBox").hide()
			isPornum++
			setTimeout(() => {
				if ($("#threesixty_images").html() == "") {
					cp('5601150180')
				}
			}, 500)
		}, 0)
		return false;
	}
}
